// # Playlist concept

import { fromJS } from 'immutable';
import { get, map, isNil, flatten, shuffle, isArray } from 'lodash';

import { getUser } from './user';
import {
  fetchTopArtistsTopTracks,
  fetchRelatedArtistsTopTracks,
  fetchTopTracksRelatedArtistsTopTracks,
  fetchRecommededTracksBasedOnTopArtists,
  fetchRecommededTracksBasedOnTopTracks,
  getTopTracksUris,
  getTimeRange,
} from './top-history';
import { getRecentlyPlayedUris } from './play-history';
import { openPlaylistPopup, setPlaylistImages } from './playlist-popup';
import { apiCall } from '../services/api';
import getPlaylistName from '../services/playlist-name';
import PlaylistTypes from '../constants/PlaylistTypes';

// # Action Types
const CREATE_PLAYLIST = 'playlist/CREATE_PLAYLIST';
const CREATE_PLAYLIST_SUCCESS = 'playlist/CREATE_PLAYLIST_SUCCESS';

const GET_PLAYLIST_IMAGE = 'playlist/GET_PLAYLIST_IMAGE';

const ADD_TRACKS_TO_PLAYLIST = 'playlist/ADD_TRACKS_TO_PLAYLIST';

// # Selectors
export const getCreatingPlayListStatus = state => state.playList.get('isCreatingPlaylist');
export const getPlaylistImages = state => state.playList.get('playlistImages');

// # Action Creators
export const createPlaylist = (params = {}) => (dispatch, getState) => {
  const user = getUser(getState());
  const userId = user.get('id');

  if (!userId) {
    return null;
  }

  return dispatch(
    apiCall({
      type: CREATE_PLAYLIST,
      url: `/users/${userId}/playlists`,
      method: 'POST',
      data: params,
    })
  );
};

export const fetchPlaylistImages = playlistId => (dispatch, getState) => {
  const user = getUser(getState());
  const userId = user.get('id');

  if (!userId) {
    return null;
  }

  return dispatch(
    apiCall({
      type: GET_PLAYLIST_IMAGE,
      url: `/users/${userId}/playlists/${playlistId}/images`,
      method: 'GET',
    })
  );
};

export const fetchNewPlaylistImage = playlistId => dispatch =>
  dispatch(fetchPlaylistImages(playlistId)).then(action =>
    dispatch(setPlaylistImages(action.payload.data))
  );

export const addTracksToPlayList = (playlistId, tracks) => (dispatch, getState) => {
  const user = getUser(getState());
  const userId = user.get('id');

  if (!userId) {
    return null;
  }

  return dispatch(
    apiCall({
      type: ADD_TRACKS_TO_PLAYLIST,
      url: `users/${userId}/playlists/${playlistId}/tracks`,
      method: 'POST',
      data: { uris: tracks },
    })
  );
};

export const finishPlaylistCreation = (response, tracks) => dispatch => {
  const playlist = get(response, 'payload.data');
  const playlistId = get(playlist, 'id');
  const playlistUri = get(playlist, 'uri');

  if (isNil(playlistId) || !tracks.length) {
    return null;
  }

  return dispatch(addTracksToPlayList(playlistId, tracks)).then(() => {
    dispatch(openPlaylistPopup(playlistUri));
    return dispatch(fetchNewPlaylistImage(playlistId));
  });
};

const topPerArtist = 5;
export const createTopArtistPlaylist = () => (dispatch, getState) => {
  let tracks;

  const timeRange = getTimeRange(getState()).get('artists');

  return dispatch(fetchTopArtistsTopTracks())
    .then(responses => {
      const tracksPerArtist = responses.map(response => get(response, 'payload.data.tracks'));

      const trackUris = tracksPerArtist.map(artistTracks =>
        artistTracks.slice(0, topPerArtist).map(track => get(track, 'uri'))
      );

      tracks = shuffle(flatten(trackUris));

      if (!tracks.length) {
        return Promise.reject(null);
      }
    })
    .then(() =>
      dispatch(
        createPlaylist({
          name: getPlaylistName({ type: PlaylistTypes.ARTIST, timeRange }),
          description: 'Top-5 tracks from each of my Top-20 artists.',
        })
      )
    )
    .then(response => dispatch(finishPlaylistCreation(response, tracks)));
};

export const createRecommendedArtistsPlaylist = () => (dispatch, getState) => {
  const timeRange = getTimeRange(getState()).get('artists');
  // recommendations can be get for seed items 5 per time,
  // so for 20 we need 4 sets
  const sets = [0, 1, 2, 3];

  return Promise.all(
    sets.map(page => dispatch(fetchRecommededTracksBasedOnTopArtists(10, page)))
  ).then(responses => {
    const tracksUris = flatten(
      responses.map(response => {
        const tracks = get(response, 'payload.data.tracks');
        return map(tracks, track => get(track, 'uri'));
      })
    );

    return dispatch(
      createPlaylist({
        name: getPlaylistName({ type: PlaylistTypes.RECOMMENDED_ARTISTS, timeRange }),
        description: '40 recommended tracks based on your Top-20 artists.',
      })
    ).then(response => dispatch(finishPlaylistCreation(response, tracksUris)));
  });
};

export const createRelatedArtistsPlaylist = () => (dispatch, getState) => {
  let tracks;

  const timeRange = getTimeRange(getState()).get('artists');

  return dispatch(fetchRelatedArtistsTopTracks())
    .then(responses => {
      const tracksPerArtist = responses.map(response => get(response, 'payload.data.tracks'));

      const trackUris = tracksPerArtist
        .filter(tracks => isArray(tracks))
        .map(artistTracks => artistTracks.slice(0, topPerArtist).map(track => get(track, 'uri')));

      tracks = shuffle(flatten(trackUris));

      if (!tracks.length) {
        return Promise.reject(null);
      }
    })
    .then(() =>
      dispatch(
        createPlaylist({
          name: getPlaylistName({ type: PlaylistTypes.RELATED_ARTISTS, timeRange }),
          description: 'Top-5 tracks from each of my Top-20 artist related artists.',
        })
      )
    )
    .then(response => dispatch(finishPlaylistCreation(response, tracks)));
};

export const createTopTracksPlaylist = () => (dispatch, getState) => {
  const state = getState();
  const tracks = getTopTracksUris(state);
  const timeRange = getTimeRange(state).get('tracks');

  if (!tracks.size) {
    return;
  }

  return dispatch(
    createPlaylist({
      name: getPlaylistName({ type: PlaylistTypes.TRACK, timeRange }),
    })
  ).then(response => dispatch(finishPlaylistCreation(response, tracks)));
};

export const createRecommendedTracksPlaylist = () => (dispatch, getState) => {
  const timeRange = getTimeRange(getState()).get('tracks');
  // recommendations can be get for seed items 5 per time,
  // so for 20 we need 4 sets
  const sets = [0, 1, 2, 3];

  return Promise.all(
    sets.map(page => dispatch(fetchRecommededTracksBasedOnTopArtists(10, page)))
  ).then(responses => {
    const tracksUris = flatten(
      responses.map(response => {
        const tracks = get(response, 'payload.data.tracks');
        return map(tracks, track => get(track, 'uri'));
      })
    );

    return dispatch(
      createPlaylist({
        name: getPlaylistName({ type: PlaylistTypes.RECOMMENDED_TRACKS, timeRange }),
        description: '40 recommended tracks based on your Top-20 tracks.',
      })
    ).then(response => dispatch(finishPlaylistCreation(response, tracksUris)));
  });
};

export const createTracksRelatedArtistsPlaylist = () => (dispatch, getState) => {
  let tracks;

  const timeRange = getTimeRange(getState()).get('artists');

  return dispatch(fetchTopTracksRelatedArtistsTopTracks())
    .then(responses => {
      const tracksPerArtist = responses.map(response => get(response, 'payload.data.tracks'));

      const trackUris = tracksPerArtist
        .filter(tracks => isArray(tracks))
        .map(artistTracks => artistTracks.slice(0, topPerArtist).map(track => get(track, 'uri')));

      tracks = shuffle(flatten(trackUris));

      if (!tracks.length) {
        return Promise.reject(null);
      }
    })
    .then(() =>
      dispatch(
        createPlaylist({
          name: getPlaylistName({ type: PlaylistTypes.RELATED_TRACKS_ARTISTS, timeRange }),
          description: 'Top-5 tracks from each of my Top-20 tracks related artists.',
        })
      )
    )
    .then(response => dispatch(finishPlaylistCreation(response, tracks)));
};

export const createRecentlyPlayedPlaylist = () => (dispatch, getState) => {
  const tracks = getRecentlyPlayedUris(getState());

  if (!tracks.size) {
    return;
  }

  return dispatch(
    createPlaylist({
      name: getPlaylistName({ type: PlaylistTypes.RECENT }),
    })
  ).then(response => dispatch(finishPlaylistCreation(response, tracks.toJS())));
};

// # Reducer
const initialState = fromJS({
  isCreatingPlaylist: false,
});

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case CREATE_PLAYLIST: {
      return state.set('isCreatingPlaylist', true);
    }

    case CREATE_PLAYLIST_SUCCESS: {
      return state.set('isCreatingPlaylist', false);
    }

    default: {
      return state;
    }
  }
}



