import React from 'react';

import ListPage from '../ListPage';
import TopHistoryTrack from '../TopHistoryTrack';
import TopHistoryArtist from '../TopHistoryArtist';
import TimeRangeSelector from '../TimeRangeSelector';
import ListActionPanel from '../ListActionPanel';
import ThemeColors from '../../constants/ThemeColors';
import { labels } from '../../constants/TimeRanges';
import PlaylistTypes from '../../constants/PlaylistTypes';
import './TopHistory.css';

const showMax = 50;
const artistImg = require('../../assets/images/top-artists.jpg');
const trackImg = require('../../assets/images/top-tracks.jpg');

const TopHistory = ({
  timeRange,
  topHistory,
  createArtistPlaylist,
  createTracksPlaylist,
  createRelatedPlaylist,
  createRecommendedPlaylist,
  updateTimeRange,
  downloadImage,
  type = PlaylistTypes.ARTIST,
}) => (
  <div className="top-history">
    {type === PlaylistTypes.ARTIST && (
      <ListPage
        headerImageSrc={artistImg}
        title="Top Artists"
        themeColor={ThemeColors.PINK}
        downloadImage={downloadImage}
      >
        <div>
          <TimeRangeSelector selected={timeRange} onSelect={updateTimeRange} />

          {topHistory
            .get('artists')
            .slice(0, showMax)
            .map((artist, index) => (
              <TopHistoryArtist
                orderNumber={index + 1}
                key={`${timeRange}-${artist.get('id')}`}
                artist={artist}
              />
            ))}

          {topHistory.get('artists').size > 0 && (
            <ListActionPanel
              title="Create Your Top Artists playlist"
              description={`This creates a playlist from your ${
                labels[timeRange]
              } Top-20 artists with Top-5 tracks from each artist
                in random order.`}
              onActionClick={createArtistPlaylist}
              secondaryActions={[
                {
                  title: 'Recommendation playlist',
                  onClick: createRecommendedPlaylist,
                  subtitle: `Create recommendation playlist from your ${
                    labels[timeRange]
                  } Top-20 artists.`,
                  icon: 'ion-android-favorite',
                },
                {
                  title: 'Similar artists',
                  onClick: createRelatedPlaylist,
                  subtitle: `Create playlist with similar artists than on your ${
                    labels[timeRange]
                  } Top-20 artists.`,
                  icon: 'ion-android-compass',
                },
                {
                  title: 'Share image',
                  subtitle: `Download and share image of your ${labels[timeRange]} Top-10 artists.`,

                  onClick: downloadImage,
                  icon: 'ion-image',
                },
              ]}
            />
          )}
        </div>
      </ListPage>
    )}
    {type === PlaylistTypes.TRACK && (
      <ListPage
        headerImageSrc={trackImg}
        title="Top Tracks"
        themeColor={ThemeColors.BLUE}
        downloadImage={downloadImage}
      >
        <div>
          <TimeRangeSelector selected={timeRange} onSelect={updateTimeRange} />
          {topHistory
            .get('tracks')
            .slice(0, showMax)
            .map((track, index) => (
              <TopHistoryTrack
                orderNumber={index + 1}
                key={`${timeRange}-${track.get('id')}`}
                track={track}
              />
            ))}

          {topHistory.get('tracks').size > 0 && (
            <ListActionPanel
              title="Create Your Top Tracks playlist"
              description={`This creates a playlist from your ${labels[timeRange]} Top-50 tracks.`}
              onActionClick={createTracksPlaylist}
              secondaryActions={[
                {
                  title: 'Recommendation playlist',
                  onClick: createRecommendedPlaylist,
                  subtitle: `Create recommendation playlist from your ${
                    labels[timeRange]
                  } Top-20 tracks.`,
                  icon: 'ion-android-favorite',
                },
                {
                  title: 'Similar artists',
                  onClick: createRelatedPlaylist,
                  subtitle: `Create playlist with similar artists than performers of your ${
                    labels[timeRange]
                  } Top-20 tracks.`,
                  icon: 'ion-android-compass',
                },
                {
                  title: 'Share image',
                  subtitle: `Download and share image of your ${labels[timeRange]} Top-10 trakcs.`,

                  onClick: downloadImage,
                  icon: 'ion-image',
                },
              ]}
            />
          )}
        </div>
      </ListPage>
    )}
  </div>
);

export default TopHistory;



